.search {
  font-family: 'lato'; }
  .search .container {
    width: 907px;
    margin-left: 50%;
    -webkit-transform: translateX(calc(-907px / 2));
        -ms-transform: translateX(calc(-907px / 2));
            transform: translateX(calc(-907px / 2));
    display: grid;
    grid-template-columns: repeat(3, 3FR);
    background: -webkit-gradient(linear, left top, right top, from(transparent), color-stop(transparent), to(#2B2C2E));
    background: -webkit-linear-gradient(left, transparent, transparent, #2B2C2E);
    background: -o-linear-gradient(left, transparent, transparent, #2B2C2E);
    background: linear-gradient(90deg, transparent, transparent, #2B2C2E);
    margin-top: 15px;
    margin-bottom: 15px; }
  .search .column {
    grid-column: 1/2;
    background: #262729;
    height: 100vh; }
  .search .results {
    grid-column: 2/4;
    height: 100%; }



